body, html {
  * {
    font-family: Helvetica Neue,sans-serif !important;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-family: HelveticaNeue-CondensedBold,Helvetica,sans-serif !important;
}

h1 {
  font-size: 80px;
  line-height: 90px;
  margin: 0;
}

.btn {
  background: #fc3;
  background-color: #fc3;
}
.btn-group {
  &,
  &.open {
    .btn {
      background: #f5df67;
      background-color: #f5df67;
    }
  }
  .dropdown-toggle {
    box-shadow: none;
  }
}

.btn {
  &:active,
  &:focus,
  &:hover {
    background: #f5df67;
    background-color: #f5df67;
  }
}
.dropdown-menu {
  border-radius: 4px;
  background: rgba(242, 242, 242, 0.9);
}

.nav-list {
  > .active {
    > a {
      &,
      &:hover,
      &:active {
        border-bottom: 2px solid #fc3;
        opacity: 1 !important;
      }
    }
  }
}

#sidenav #scrollingNav .sidenav li {
  background: none;
}
